// import { FARMS } from "../../app/shared/FARMS";
import { Col, Row } from "reactstrap";
import FarmCard from "./FarmCard";
import { selectAllFarms } from "./farmsSlice";
import { useSelector } from "react-redux";
import Error from "../../components/Error";
import Loading from "../../components/Loading";

const FarmsList = () => {
  const farms = useSelector(selectAllFarms);
  console.log("farms:", farms);
  const isLoading = useSelector((state) => state.farms.isLoading);
  const errMsg = useSelector((state) => state.farms.errMsg);

  if (isLoading) {
    return (
      <Row>
        <Loading />
      </Row>
    );
  }

  if (errMsg) {
    return (
      <Row>
        <Error errMsg={errMsg} />
      </Row>
    );
  }

  return (
    <Row className="ms-auto">
      {farms.map((farm) => {
        return (
          <Col md="5" className="m-4" key={farm.id}>
            <FarmCard farm={farm} />
          </Col>
        );
      })}
    </Row>
  );
};

export default FarmsList;
