// import { FARMS } from "../../app/shared/FARMS";
import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import { baseUrl } from "../../app/shared/baseUrl";
import { mapImageURL } from "../../utils/mapImageURL";

export const fetchFarms = createAsyncThunk("farms/fetchFarms", async () => {
  const response = await fetch(baseUrl + "farms");
  if (!response.ok) {
    return Promise.reject("Unable to fetch E1001, status: " + response.status);
  }
  const data = await response.json();
  return data;
});

const initialState = {
  farmsArray: [],
  isLoading: true,
  errMsg: "",
};

const farmsSlice = createSlice({
  name: "farms",
  initialState,
  reducers: {},
  extraReducers: {
    [fetchFarms.pending]: (state) => {
      state.isLoading = true;
    },
    [fetchFarms.fulfilled]: (state, action) => {
      state.isLoading = false;
      state.errMsg = "";
      state.farmsArray = mapImageURL(action.payload);
    },
    [fetchFarms.rejected]: (state, action) => {
      state.isLoading = false;
      state.errMsg = action.err
        ? action.error.message + " E1000.1"
        : "ERROR E1000.2";
    },
  },
});

export const farmsReducer = farmsSlice.reducer;

export const selectAllFarms = (state) => {
  return state.farms.farmsArray;
};

export const selectFarmById = (id) => (state) => {
  return state.farms.farmsArray.find((farm) => farm.id === parseInt(id));
};

export const selectFeaturedFarm = (state) => {
  return {
    featuredItem: state.farms.farmsArray.find((farm) => farm.featured),
    isLoading: state.farms.isLoading,
    errMsg: state.farms.errMsg,
  };
};
