import { Routes, Route } from "react-router-dom";
import { useEffect } from "react";
import { useDispatch } from "react-redux";
import FarmsDirectoryPage from "./pages/FarmsDirectoryPage";
import FarmDetailPage from "./pages/FarmDetailPage";
import ContactPage from "./pages/ContactPage";
import HomePage from "./pages/HomePage";
import Header from "./components/Header.js";
import Footer from "./components/Footer";
import AboutPage from "./pages/AboutPage.js";
import "./App.css";
import { fetchFarms } from "./features/farms/farmsSlice.js";
import { fetchPartners } from "./features/partners/partnersSlice.js";
import { fetchPromotions } from "./features/promotions/promotionsSlice.js";

function App() {
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(fetchFarms());
    dispatch(fetchPartners());
    dispatch(fetchPromotions());
  }, [dispatch]);

  return (
    <div className="App">
      <Header />
      <Routes>
        <Route path="/" element={<HomePage />} />
        <Route path="contact" element={<ContactPage />} />
        <Route path="directory" element={<FarmsDirectoryPage />} />
        <Route path="directory/:farmId" element={<FarmDetailPage />} />
        <Route path="about" element={<AboutPage />} />
      </Routes>
      <Footer />
    </div>
  );
}

export default App;
