import { Container, Row } from "reactstrap";
import { useParams } from "react-router-dom";
import { selectFarmById } from "../features/farms/farmsSlice";
import FarmDetail from "../features/farms/FarmDetail";
import CommentsList from "../features/comments/CommentsList";
import SubHeader from "../components/SubHeader";
import { useSelector } from "react-redux";
import Error from "../components/Error";
import Loading from "../components/Loading";

const FarmDetailPage = () => {
  const { farmId } = useParams();
  const farm = useSelector(selectFarmById(farmId));

  const isLoading = useSelector((state) => state.farms.isLoading);
  const errMsg = useSelector((state) => state.farms.errMsg);
  let content = null;

  if (isLoading) {
    content = <Loading />;
  } else if (errMsg) {
    content = <Error errMsg={errMsg} />;
  } else {
    content = (
      <>
        <FarmDetail farm={farm} />
        <CommentsList farmId={farmId} />
      </>
    );
  }

  return (
    <Container>
      {farm && <SubHeader current={farm.name} detail={true} />}
      <Row>{content}</Row>
    </Container>
  );
};

export default FarmDetailPage;
